<template>
    <div class="analysis-section">
        <div class="section-header">
            <h2>   </h2>
            <div class="date-filter">
                <SelectLive :liveList="liveHistory" @selected="handleSelectedLive" />
            </div>
        </div>

        <!-- selectedLiveId     -->
        <div v-if="selectedLiveId" class="top-chatters">
            <div class="chatter-list">
                <div v-for="(chatter, index) in topChattersData" :key="chatter.nickname" 
                     class="chatter-item"
                     :class="{'top-three': index < 3}">
                    <div class="rank" :class="{'top-rank': index < 3}">{{ index + 1 }}</div>
                    <div class="profile-image">
                        <img :src="require('@/assets/anonymous.png')" :alt="chatter.nickname">
                        <div v-if="index < 3" class="crown-icon"></div>
                    </div>
                    <div class="chatter-info">
                        <div class="nickname">{{ chatter.nickname }}</div>
                        <div class="chat-stats">
                            <div class="chat-count"> {{ chatter.count.toLocaleString() }}</div>
                            <div class="chat-percentage">
                                  {{ calculatePercentage(chatter.count) }}%
                            </div>
                        </div>
                        <div class="progress-bar">
                            <div class="progress" :style="{ width: calculatePercentage(chatter.count) + '%' }"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- selectedLiveId      -->
        <div v-else class="top-chatters-placeholder">
             .
        </div>
    </div>
</template>

<script>
import SelectLive from '../../common/selectLive.vue';
import axiosInstance from '@/axios_config/axiosConfig';

export default {
    name: 'TopChatters',
    components: {
        SelectLive,
    },
    props: {
        channelId: {
            type: String,
            required: true
        },
        liveHistory: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            topChattersData: [],
            selectedLiveId: ''
        }
    },
    mounted() {
        console.log('TopChatters mounted, liveHistory:', this.liveHistory);
    },
    methods: {
        handleSelectedLive(selectedId) {
            this.selectedLiveId = selectedId;
            if (this.selectedLiveId) {
                this.fetchTopChatters();
            } else {
                this.topChattersData = [];
            }
        },
        async fetchTopChatters() {
            if (!this.selectedLiveId) {
                this.topChattersData = [];
                return;
            }
            try {
                const response = await axiosInstance.get(`/chat/analytics/top_user`, {
                    params: {
                        channel_id: this.channelId,
                        live_key: this.selectedLiveId
                    }
                });
                this.topChattersData = response.data.content.top_users || [];
            } catch (error) {
                console.error('[FetchTopChatters] API Error:', error);
                this.topChattersData = [];
            }
        },
        calculatePercentage(chatCount) {
            if (!this.topChattersData.length) return 0;
            const totalChats = this.topChattersData.reduce((sum, chatter) => sum + chatter.count, 0);
            return ((chatCount / totalChats) * 100).toFixed(1);
        },
    }
}
</script>

