<template>
  <div class="signup-page">
    <div class="container">
      <h1></h1>  
      <form id="signupForm" @submit.prevent="submitForm">

        <div class="input-group">
          <label for="name" :class="{ active: user.username || nameIsFocused }" class="required-field"></label>
          <input 
            type="text" 
            id="name" 
            v-model="user.username" 
            required
            @focus="nameIsFocused = true"
            @blur="nameIsFocused = false"
            :class="{ error: !isNameValid && name !== '', valid: isNameValid && name !== '' }"
          >
        </div>
        
        <div class="input-group">
          <label for="email" :class="{ active: user.email || emailIsFocused }" class="required-field"></label>
          <input 
            type="email" 
            id="email" 
            v-model="user.email" 
            required
            @focus="emailIsFocused = true"
            @blur="emailIsFocused = false"
            :class="{ error: !isEmailValid && email !== '', valid: isEmailValid && email !== '' }"
          >
        </div>
        
        <div class="input-group">
          <label for="password" :class="{ active: user.password || passwordIsFocused }" class="required-field"></label>
          <input 
            type="password" 
            id="password" 
            v-model="user.password" 
            required
            @focus="showPasswordRequirements = true; passwordIsFocused = true"
            @blur="passwordIsFocused = false"
            :class="{ error: !isPasswordValid && user.password !== '', valid: isPasswordValid && user.password !== '' }"
          >
          <div class="pwd-requirements" :class="{ show: showPasswordRequirements && user.password !== '' }">
            <div class="requirement" :class="{ valid: isLengthValid }">
              <span class="requirement-icon">{{ isLengthValid ? '' : '' }}</span>
              <span> 8  </span>
            </div>
            <div class="requirement" :class="{ valid: isCharsValid }">
              <span class="requirement-icon">{{ isCharsValid ? '' : '' }}</span>
              <span>, ,   </span>
            </div>
            <div class="requirement" :class="{ valid: isSpecialValid }">
              <span class="requirement-icon">{{ isSpecialValid ? '' : '' }}</span>
              <span>12      </span>
            </div>
          </div>
        </div>
        
        <div class="input-group">
          <label for="confirm-password" :class="{ active: confirmPassword || confirmPasswordIsFocused }" class="required-field"> </label>
          <input 
            type="password" 
            id="confirm-password" 
            v-model="confirmPassword" 
            required
            @focus="confirmPasswordIsFocused = true"
            @blur="confirmPasswordIsFocused = false"
            :class="{ error: !passwordsMatch && confirmPassword !== '', valid: passwordsMatch && confirmPassword !== '' }"
          >
        </div>
        
        <div class="terms-section">
          <div class="terms-container">
            <div class="terms-title">
              <span>  </span>
            </div>
            <div class="terms-checkbox">
              <input type="checkbox" id="terms-all" v-model="termsAll" @change="updateTerms">
              <label for="terms-all">  </label>
            </div>
          </div>
          
          <div class="terms-container" v-for="(term, index) in terms" :key="index">
            <div class="terms-title" @click="toggleTerms(index)">
              <span>{{ term.title }}&nbsp;&nbsp;&nbsp;&nbsp;</span>
              <span :class="term.required ? 'required-tag' : 'optional-tag'">{{ term.required ? '()' : '()' }}</span>
              <span class="toggle-icon" :class="{ rotated: term.show }"></span>
            </div>
            <div class="terms-content" :class="{ show: term.show }">
              <p v-for="(paragraph, pIndex) in term.content" :key="pIndex">{{ paragraph }}</p>
            </div>
            <div class="terms-checkbox">
              <input 
                type="checkbox" 
                :id="'terms' + (index + 1)" 
                v-model="term.agreed" 
                @change="updateTermsAllCheckbox"
                :required="term.required"
              >
              <label :for="'terms' + (index + 1)"></label>
            </div>
          </div>
        </div>
        
        <button type="submit" :disabled="!isFormValid"></button>
      </form>
    </div>
  </div>  
</template>

<script>
import axiosInstance from '@/axios_config/axiosConfig';

export default {
  data() {
    return {
      user:{
        username: '',
        email: '',
        password: '',
      },
      confirmPassword: '',
      
      nameIsFocused: false,
      emailIsFocused: false,
      passwordIsFocused: false,
      confirmPasswordIsFocused: false,
      
      showPasswordRequirements: false,
      termsAll: false,


      terms: [
        {
          title: '  ',
          required: true,
          show: false,
          agreed: false,
          content: [
            '1 ()          ,   ,      .',
            '2 ()       . 1. ""      . 2. ""           .'
          ]
        },
        {
          title: '   ',
          required: true,
          show: false,
          agreed: false,
          content: [
            '1.   : , , , , ',
            '2.   :   ,    ,   ',
            '3.   :    (     )'
          ]
        },
        {
          title: '   ',
          required: false,
          show: false,
          agreed: false,
          content: [
            '1.   : , ',
            '2.   :    ,    ',
            '3.   :        '
          ]
        }
      ]
    };
  },
  
  computed: {
    isNameValid() {
      return this.user.username !== '';
    },
    
    isEmailValid() {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return emailRegex.test(this.user.email);
    },
    
    isLengthValid() {
      return this.user.password.length >= 8;
    },
    
    isCharsValid() {
      return /[A-Z]/.test(this.user.password) && 
             /[a-z]/.test(this.user.password) && 
             /[0-9]/.test(this.user.password);
    },
  
    isSpecialNeeded() {
      return this.user.password.length < 12;
    },
    
    hasSpecialChar() {
      // eslint-disable-next-line no-useless-escape
      return /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/.test(this.user.password);
    },
    
    isSpecialValid() {
      return !this.isSpecialNeeded || this.hasSpecialChar;
    },
    
    isPasswordValid() {
      if (this.user.password === '') return false;
      return this.isLengthValid && this.isCharsValid && this.isSpecialValid;
    },
    
    passwordsMatch() {
      return this.user.password !== '' && this.user.password === this.confirmPassword;
    },
    
    areRequiredTermsAgreed() {
      return this.terms.filter(term => term.required).every(term => term.agreed);
    },
    
    isFormValid() {
      return this.isNameValid && 
             this.isEmailValid && 
             this.isPasswordValid && 
             this.passwordsMatch && 
             this.areRequiredTermsAgreed;
    }
  },
  
  methods: {
    toggleTerms(index) {
      this.terms[index].show = !this.terms[index].show;
    },
    
    updateTerms() {
      this.terms.forEach(term => {
        term.agreed = this.termsAll;
      });
    },
    
    updateTermsAllCheckbox() {
      this.termsAll = this.terms.every(term => term.agreed);
    },
    
    async submitForm() {
      if (this.isFormValid) {
        try {
          const response = await axiosInstance.post('/user/register', {
                    username: this.user.username,
                    email: this.user.email,
                    password: this.user.password,
                });
                console.log(response.data);
                 if (response.data.status_code === 200) {
                     window.location = "/login"
                 } else {
                     console.error('Failed register');
                 }
             } catch (error) {
                 console.error('Error register:', error);
             }
          }
        }
      }
    };
    
</script>

