<template>
  <div class="highlight-analysis">
    <div class="left-section">
      <div class="time-selector">
        <SelectLive :liveList="liveHistory" @selected="handleSelectedLive" />
        <select v-model="period" @change="fetchHighlightData">
          <option value="10min">10</option>
          <option value="30min">30</option>
          <option value="hour"></option>
          <option value="day"></option>
          <option value="month"></option>
          <option value="year"></option>
          <option value="all"></option>
        </select>
      </div>
      <div class="graph-container" ref="graphContainer"></div>
    </div>
    <div class="right-section">
      <div class="highlight-blocks">
        <div v-for="(highlight, index) in highlightData" :key="index" 
             class="highlight-block" 
             :class="{ 'expanded': selectedHighlightIndex === index }"
             @click="toggleHighlight(index)">
          <div class="block-header">
            <span class="block-time">{{ formatTime(highlight._id) }}</span>
            <span class="block-count">{{ highlight.count }}</span>
          </div>
          <div class="block-details" v-if="selectedHighlightIndex === index">
            <div class="detail-item">
              <span class="label"> :</span>
              <span class="value">{{ highlight.count }}</span>
            </div>
            <div class="detail-item">
              <span class="label">  :</span>
              <span class="value">{{ highlight.avg_chat_count?.toFixed(2) }}</span>
            </div>
            <div class="detail-item">
              <span class="label"> upper_band:</span>
              <span class="value">{{ highlight.avg_upper_band?.toFixed(2) }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axiosInstance from '@/axios_config/axiosConfig';
import * as echarts from 'echarts';
import SelectLive from '../../common/selectLive.vue';

export default {
  name: 'HighlightAnalysis',
  components: {
    SelectLive
  },
  props: {
    liveHistory: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      period: 'hour',
      chart: null,
      selectedHighlight: null,
      selectedLiveId: null
    }
  },
  methods: {
    async handleSelectedLive(selectedId) {
      this.selectedLiveId = selectedId;
      if (this.selectedLiveId !== null) {
        this.fetchHighlightData();
      }
    },
    async fetchHighlightData() {
      try {
        const response = await axiosInstance.get(`/chzzk/hightlights/summary/${this.$route.params.channelId}`, {
          params: {
            live_key: this.selectedLiveId,
            period: this.period
          }
        });
        if (response.data.status_code === 200) {
          this.highlightData = await response.data.content.highlights;
          this.updateGraph();
        } else {
          console.error('   :', response.data.detail);
        }
      } catch (error) {
        console.error('   :', error);
      }
    },
    toggleHighlight(index) {
      this.selectedHighlightIndex = this.selectedHighlightIndex === index ? null : index;
    },
    updateGraph() {
      if (!this.highlightData || !this.highlightData.length) {
        if (this.chart) {
          this.chart.clear();
        }
        return;
      }

      if (!this.chart) {
        this.chart = echarts.init(this.$refs.graphContainer);
      }

      const data = this.highlightData.map(item => {
        let dateStr = item._id;
        if (/^\d{4}-\d{2}-\d{2} \d{2}$/.test(dateStr)) {
          dateStr = dateStr.replace(' ', 'T') + ':00:00';
        } else if (/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}$/.test(dateStr)) {
          dateStr = dateStr.replace(' ', 'T') + ':00';
        } else {
          dateStr = dateStr.replace(' ', 'T');
        }
        return {
          value: [new Date(dateStr), item.count],
          itemStyle: { color: '#4CAF50' },
          time_unit: item._id,
          highlight_occurrences: item.count,
          avg_upper_band: item.avg_upper_band,
          avg_chat_count: item.avg_chat_count
        };
      });

      const option = {
        backgroundColor: '#2a2a2a',
        title: {
          text: '   ',
          textStyle: {
            color: '#ffffff'
          }
        },
        tooltip: {
          trigger: 'axis',
          backgroundColor: 'rgba(50, 50, 50, 0.9)',
          borderColor: '#555',
          textStyle: {
            color: '#fff'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%', 
          containLabel: true
        },
        xAxis: {
          type: 'time',
          boundaryGap: false,
          axisLine: {
            lineStyle: {
              color: '#555'
            }
          },
          axisLabel: {
            color: '#fff'
          }
        },
        yAxis: {
          type: 'value',
          name: '  ',
          nameTextStyle: {
            color: '#ccc'
          },
          axisLine: {
            lineStyle: {
              color: '#555'
            }
          },
          axisLabel: {
            color: '#fff'
          },
          splitLine: {
            lineStyle: {
              color: '#555'
            }
          }
        },
        series: [{
          name: '',
          type: 'line',
          smooth: true,
          data: data.map(item => item.value),
          itemStyle: {
            color: '#4CAF50'
          },
          areaStyle: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
              offset: 0,
              color: 'rgba(76, 175, 80, 0.3)'
            }, {
              offset: 1,
              color: 'rgba(76, 175, 80, 0.1)'
            }])
          }
        }]
      };

      this.chart.setOption(option);
    },
    formatTime(timestamp) {
      let dateStr = timestamp;
      if (/^\d{4}-\d{2}-\d{2} \d{2}$/.test(dateStr)) {
        dateStr = dateStr.replace(' ', 'T') + ':00:00';
      } else if (/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}$/.test(dateStr)) {
        dateStr = dateStr.replace(' ', 'T') + ':00';
      } else {
        dateStr = dateStr.replace(' ', 'T');
      }
      const date = new Date(dateStr);
      return date.toLocaleString('ko-KR', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      });
    }
  },
  mounted() {
    // this.fetchHighlightData();
    window.addEventListener('resize', () => {
      if (this.chart) {
        this.chart.resize();
      }
    });
  },
  beforeUnmount() {
    if (this.chart) {
      this.chart.dispose();
    }
    window.removeEventListener('resize', this.updateGraph);
  }
}
</script>

